.footer {
    background-color: #1D1D24;
    padding: 20px 0; /* Reduced padding for small screens */
    color: #fff;
    font-family: 'Inter', sans-serif;
  }
  
  .footerContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 10px; /* Reduced padding for small screens */
  }
  
  .logoContainer {
    margin-bottom: 20px;
  }
  
  .logo {
    height: 50px;
  }
  
  .links {
    display: flex;
    flex-direction: column; /* Stack links vertically on small screens */
    align-items: center;
    gap: 10px; /* Reduced gap for small screens */
    margin-bottom: 20px;
  }
  
  .footerLink {
    background: none;
    border: none;
    color: #54545A;
    font-weight: 500;
    text-decoration: none;
    cursor: pointer;
    transition: color 0.3s ease;
    font-size: 15px;
    margin-bottom: 10px;
  }
  
  .footerLink:hover {
    color: #4ABF85;
  }
  
  .socials {
    margin-bottom: 20px;
  }
  
  .socialLink img {
    height: 24px;
    transition: transform 0.3s ease;
  }
  
  .socialLink:hover img {
    transform: scale(1.1);
  }
  
  .footerBottom {
    text-align: center;
    padding-top: 20px;
    border-top: 1px solid #323235;
    margin-top: 20px;
    color: #404045;
  }
  
  /* Modal styles */
  
  .modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modalContent {
    background-color: #1D1D24;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    width: 90%; /* Adjusted for small screens */
    max-width: 500px;
    position: relative;
  }
  
  .closeButton {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    color: #fff;
    font-size: 24px;
    cursor: pointer;
  }
  
  .closeButton:hover {
    color: #4ABF85;
  }
  
  h2 {
    margin-top: 0;
    color: #4ABF85;
  }

  h3 {
    font-size: 14px;
    font-weight: 400;
  }
  
  p {
    color: #404045;
  }
  
  @media (max-width: 600px) {
    .footer {
      padding: 20px 0;
    }
    
    .footerContent {
      padding: 0 10px;
    }
    
    .links {
      gap: 10px;
    }
    
    .footerBottom {
      padding-top: 15px;
      margin-top: 15px;
    }
  }
